<template>
    <div class="black-bg" v-if="모달창열렸니 == true">
        <div class="white-bg">
            <img :src="제주여행지[제목누른거].image" style="width:100%">
            <h4>{{제주여행지[제목누른거].title}}</h4>
            <p>{{제주여행지[제목누른거].price.toLocaleString()}}</p>
            <p>{{제주여행지[제목누른거].content}}</p>
            <!-- @input @click이랑 같은 거  -->
            <!-- <input @input="month=$event.target.value"> -->
            <!-- 위 아래 두개 다 같은거임 -->
            <input v-model.number="priceNumber">
            <p>{{priceNumber}}개를 선택함 :{{제주여행지[제목누른거].price * priceNumber}}원</p>
            <!-- $event 뷰가 제공하는 변수임 무슨 일이 벌어졌네요라는-->
            <!-- read only기능 -->
            <!-- <button @click="$emit('modalOut')">닫기</button> -->
            <button @click="closeModal()">닫기</button>
        </div>
    </div>
</template>
<script>
export default {
    name:"Modal", //정체성 알려주는 그런느낌 title
    data(){
        return{
            // month :0,
            priceNumber :0,
        }       
    },

    beforeUpdate(){
        if(this.priceNumber ==2){
        alert("2개 이하 패키지 상품은 안팔아요!!");
        }
    },


    watch:{
        priceNumber(a){//프라이넘버데이터가 바뀔 때 마다
        //만약에 10개 이상 구매하면 11개 이상 예약하지 못합니다.
            if(a > 11){
                alert("11개 이상은 선택할 수 없어요!!!")
                this.priceNumber = (1);
            }else if(isNaN(a)==true){
                alert("문자는 안됩니다");
                this.priceNumber = (1);
            }
            //문자방지코드 문자입력시 ==> 문자입력하지마세요
            
        }
    },
    props:{
        제주여행지:Array, //부모님이 가지고 있는 데이터의 원형 데이터이름 : 자료형 숫자는 number 
        제목누른거:Number,
        모달창열렸니:Boolean
    },
    methods:{
        closeModal() {
        this.$emit("modalOut");
        this.priceNumber = "";
        }
    }

    // methods:{
    //     modalOut(){//is not defined 없다는 것도 있지만 못 알아듣겠어요도 맞다
    //                 //is not defined ===>this 꼭 붙이기
    //         this.$emit('close-modal');
    //     }
    // }
}
</script>
